.card-actions {
  display: flex;
  justify-content: space-around;
  margin-top: -20px;
}

.chip-container {
  display: flex;
  flex-flow: row wrap;
  // height: 26px; /* pre-calculated */
  height: 50px;
  // overflow: hidden;
}

.chip {
  margin: 2px 3px 0 1px; /* if top/bottom margins are changed, must calculate the chip-container's height again */
}

.description {
  height: 100px;
  overflow: hidden;
}

